"use client";

import { useCallback, useEffect, useState } from "react";

interface ConfirmModalProps {
    isOpen: boolean;
    title: string;
    message: string;
    confirmLabel?: string;
    cancelLabel?: string;
    variant?: "danger" | "warning" | "default";
    onConfirm: () => void;
    onCancel: () => void;
}

export function ConfirmModal({
    isOpen,
    title,
    message,
    confirmLabel = "Confirm",
    cancelLabel = "Cancel",
    variant = "default",
    onConfirm,
    onCancel,
}: ConfirmModalProps) {
    // Handle escape key
    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            if (!isOpen) return;
            if (e.key === "Escape") {
                onCancel();
            } else if (e.key === "Enter") {
                onConfirm();
            }
        };

        window.addEventListener("keydown", handleKeyDown);
        return () => window.removeEventListener("keydown", handleKeyDown);
    }, [isOpen, onConfirm, onCancel]);

    if (!isOpen) return null;

    const variantStyles = {
        danger: {
            icon: "üóëÔ∏è",
            buttonClass: "btn btn-danger",
            accentColor: "var(--accent-danger)",
        },
        warning: {
            icon: "‚ö†Ô∏è",
            buttonClass: "btn btn-secondary",
            accentColor: "var(--accent-warning)",
        },
        default: {
            icon: "‚ùì",
            buttonClass: "btn btn-primary",
            accentColor: "var(--accent-primary)",
        },
    };

    const styles = variantStyles[variant];

    return (
        <div
            className="modal-overlay"
            onClick={(e) => e.target === e.currentTarget && onCancel()}
            role="dialog"
            aria-modal="true"
            aria-labelledby="confirm-title"
        >
            <div
                className="modal"
                style={{
                    maxWidth: 420,
                    textAlign: "center",
                }}
            >
                <div style={{ padding: 32 }}>
                    <div
                        style={{
                            width: 64,
                            height: 64,
                            borderRadius: "50%",
                            background: `${styles.accentColor}20`,
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center",
                            margin: "0 auto 20px",
                            fontSize: 28,
                        }}
                    >
                        {styles.icon}
                    </div>

                    <h2
                        id="confirm-title"
                        style={{
                            fontSize: 20,
                            fontWeight: 600,
                            marginBottom: 12,
                        }}
                    >
                        {title}
                    </h2>

                    <p
                        style={{
                            color: "var(--text-secondary)",
                            fontSize: 14,
                            lineHeight: 1.6,
                            marginBottom: 28,
                        }}
                    >
                        {message}
                    </p>

                    <div style={{ display: "flex", gap: 12, justifyContent: "center" }}>
                        <button
                            className="btn btn-secondary"
                            onClick={onCancel}
                            style={{ minWidth: 100 }}
                        >
                            {cancelLabel}
                        </button>
                        <button
                            className={styles.buttonClass}
                            onClick={onConfirm}
                            style={{ minWidth: 100 }}
                            autoFocus
                        >
                            {confirmLabel}
                        </button>
                    </div>

                    <div
                        style={{
                            marginTop: 16,
                            fontSize: 12,
                            color: "var(--text-muted)",
                        }}
                    >
                        Press <kbd style={{
                            background: "var(--bg-tertiary)",
                            padding: "2px 6px",
                            borderRadius: 4,
                            fontSize: 11,
                        }}>Enter</kbd> to confirm or <kbd style={{
                            background: "var(--bg-tertiary)",
                            padding: "2px 6px",
                            borderRadius: 4,
                            fontSize: 11,
                        }}>Esc</kbd> to cancel
                    </div>
                </div>
            </div>
        </div>
    );
}

// Hook for easy confirm modal usage
export function useConfirm() {
    const [state, setState] = useState<{
        isOpen: boolean;
        title: string;
        message: string;
        variant: "danger" | "warning" | "default";
        resolve: ((value: boolean) => void) | null;
    }>({
        isOpen: false,
        title: "",
        message: "",
        variant: "default",
        resolve: null,
    });

    const confirm = useCallback(
        (options: {
            title: string;
            message: string;
            variant?: "danger" | "warning" | "default";
        }): Promise<boolean> => {
            return new Promise((resolve) => {
                setState({
                    isOpen: true,
                    title: options.title,
                    message: options.message,
                    variant: options.variant ?? "default",
                    resolve,
                });
            });
        },
        []
    );

    const handleConfirm = useCallback(() => {
        state.resolve?.(true);
        setState((s) => ({ ...s, isOpen: false, resolve: null }));
    }, [state.resolve]);

    const handleCancel = useCallback(() => {
        state.resolve?.(false);
        setState((s) => ({ ...s, isOpen: false, resolve: null }));
    }, [state.resolve]);

    const ConfirmDialog = () => (
        <ConfirmModal
            isOpen={state.isOpen}
            title={state.title}
            message={state.message}
            variant={state.variant}
            confirmLabel={state.variant === "danger" ? "Delete" : "Confirm"}
            onConfirm={handleConfirm}
            onCancel={handleCancel}
        />
    );

    return { confirm, ConfirmDialog };
}
