"use client";

import { useState } from "react";
import { useQuery, useMutation } from "convex/react";
import { api } from "../../../convex/_generated/api";
import { Id } from "../../../convex/_generated/dataModel";
import Navigation from "@/components/Navigation";
import { useToast } from "@/components/Toast";
import { useConfirm } from "@/components/ConfirmModal";

export default function TemplatesPage() {
    const templates = useQuery(api.templates.list);
    const createTemplate = useMutation(api.templates.create);
    const deleteTemplate = useMutation(api.templates.remove);
    const toast = useToast();
    const { confirm, ConfirmDialog } = useConfirm();

    const [showModal, setShowModal] = useState(false);
    const [name, setName] = useState("");
    const [subject, setSubject] = useState("");
    const [htmlBody, setHtmlBody] = useState("");
    const [textBody, setTextBody] = useState("");
    const [footerEnabled, setFooterEnabled] = useState(true);
    const [isSubmitting, setIsSubmitting] = useState(false);

    const handleCreate = async () => {
        if (!name.trim() || !subject.trim()) return;
        setIsSubmitting(true);
        try {
            await createTemplate({
                name: name.trim(),
                subject: subject.trim(),
                htmlBody,
                textBody,
                footerEnabled,
            });
            setShowModal(false);
            resetForm();
            toast.success("Template created successfully!");
        } catch (error) {
            console.error(error);
            toast.error("Failed to create template");
        } finally {
            setIsSubmitting(false);
        }
    };

    const resetForm = () => {
        setName("");
        setSubject("");
        setHtmlBody("");
        setTextBody("");
        setFooterEnabled(true);
    };

    const handleDelete = async (id: Id<"templates">) => {
        const confirmed = await confirm({
            title: "Delete Template?",
            message: "This action cannot be undone. Any campaigns using this template will need to be updated.",
            variant: "danger",
        });
        if (confirmed) {
            try {
                await deleteTemplate({ id });
                toast.success("Template deleted");
            } catch {
                toast.error("Failed to delete template");
            }
        }
    };

    return (
        <Navigation>
            <div>
                {/* Header */}
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", marginBottom: 32 }}>
                    <div>
                        <h1 className="page-title">Templates</h1>
                        <p className="page-subtitle">Create and manage email templates with personalization</p>
                    </div>
                    <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                        <span>+</span> New Template
                    </button>
                </div>

                {/* Templates Grid */}
                {!templates ? (
                    <div className="loading">
                        <div className="spinner" />
                    </div>
                ) : templates.length === 0 ? (
                    <div className="card">
                        <div className="empty-state">
                            <div className="empty-icon">üìù</div>
                            <h3 className="empty-title">No templates yet</h3>
                            <p className="empty-text">Create your first email template to get started</p>
                            <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                                Create Template
                            </button>
                        </div>
                    </div>
                ) : (
                    <div style={{ display: "grid", gap: 20, gridTemplateColumns: "repeat(auto-fill, minmax(350px, 1fr))" }}>
                        {templates.map((template: { _id: Id<"templates">; name: string; subject: string; htmlBody: string; textBody: string; footerEnabled: boolean }) => (
                            <div key={template._id} className="card" style={{ display: "flex", flexDirection: "column" }}>
                                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", marginBottom: 16 }}>
                                    <div>
                                        <h3 style={{ fontSize: 18, fontWeight: 600, marginBottom: 4 }}>{template.name}</h3>
                                        <p style={{ fontSize: 14, color: "var(--text-secondary)" }}>{template.subject}</p>
                                    </div>
                                    <span className={`badge ${template.footerEnabled ? "badge-success" : "badge-neutral"}`}>
                                        {template.footerEnabled ? "Footer On" : "Footer Off"}
                                    </span>
                                </div>

                                <div
                                    style={{
                                        flex: 1,
                                        background: "var(--bg-tertiary)",
                                        borderRadius: "var(--radius-md)",
                                        padding: 16,
                                        fontSize: 13,
                                        color: "var(--text-secondary)",
                                        marginBottom: 16,
                                        maxHeight: 120,
                                        overflow: "hidden",
                                        position: "relative",
                                    }}
                                >
                                    {template.textBody || template.htmlBody?.replace(/<[^>]*>/g, "").slice(0, 200) || "No content"}
                                    <div
                                        style={{
                                            position: "absolute",
                                            bottom: 0,
                                            left: 0,
                                            right: 0,
                                            height: 40,
                                            background: "linear-gradient(transparent, var(--bg-tertiary))",
                                        }}
                                    />
                                </div>

                                <div style={{ display: "flex", gap: 8 }}>
                                    <button className="btn btn-secondary btn-sm" style={{ flex: 1 }}>
                                        Edit
                                    </button>
                                    <button
                                        className="btn btn-ghost btn-sm"
                                        onClick={() => handleDelete(template._id)}
                                        style={{ color: "var(--accent-danger)" }}
                                    >
                                        Delete
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                )}

                {/* Modal */}
                {showModal && (
                    <div className="modal-overlay" onClick={(e) => e.target === e.currentTarget && setShowModal(false)}>
                        <div className="modal" style={{ maxWidth: 700 }}>
                            <div className="modal-header">
                                <h2 className="modal-title">Create Template</h2>
                                <button className="modal-close" onClick={() => setShowModal(false)}>√ó</button>
                            </div>

                            <div className="modal-body">
                                <div className="form-group">
                                    <label className="form-label">Template Name *</label>
                                    <input
                                        type="text"
                                        className="form-input"
                                        placeholder="e.g., Welcome Email"
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                    />
                                </div>

                                <div className="form-group">
                                    <label className="form-label">Subject Line *</label>
                                    <input
                                        type="text"
                                        className="form-input"
                                        placeholder="e.g., Welcome to {{firstName}}!"
                                        value={subject}
                                        onChange={(e) => setSubject(e.target.value)}
                                    />
                                    <p className="form-hint">
                                        Use {"{{firstName}}"}, {"{{lastName}}"}, {"{{email}}"} for personalization
                                    </p>
                                </div>

                                <div className="form-group">
                                    <label className="form-label">HTML Body</label>
                                    <textarea
                                        className="form-textarea"
                                        placeholder="<h1>Hello {{firstName}}!</h1>..."
                                        value={htmlBody}
                                        onChange={(e) => setHtmlBody(e.target.value)}
                                        style={{ minHeight: 180, fontFamily: "monospace", fontSize: 13 }}
                                    />
                                </div>

                                <div className="form-group">
                                    <label className="form-label">Plain Text Body</label>
                                    <textarea
                                        className="form-textarea"
                                        placeholder="Hello {{firstName}}!..."
                                        value={textBody}
                                        onChange={(e) => setTextBody(e.target.value)}
                                        style={{ minHeight: 120 }}
                                    />
                                </div>

                                <div className="form-group">
                                    <label className="form-checkbox">
                                        <input
                                            type="checkbox"
                                            checked={footerEnabled}
                                            onChange={(e) => setFooterEnabled(e.target.checked)}
                                        />
                                        <span>Include unsubscribe footer (recommended for compliance)</span>
                                    </label>
                                </div>
                            </div>

                            <div className="modal-footer">
                                <button className="btn btn-secondary" onClick={() => setShowModal(false)}>
                                    Cancel
                                </button>
                                <button
                                    className="btn btn-primary"
                                    onClick={handleCreate}
                                    disabled={!name.trim() || !subject.trim() || isSubmitting}
                                >
                                    {isSubmitting ? "Creating..." : "Create Template"}
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
            <ConfirmDialog />
        </Navigation>
    );
}
